import axios from './axios';
import qs from 'qs';

export default {
    // 获取验证码
    postPhone(phone: object) {
        return axios.post('/a/student/phone', qs.stringify(phone));
    },
    // 注册手机号，成功后返回token
    postRegister(params: object) {
        return axios.post('/a/student/register', qs.stringify(params));
    },
    // 添加用户名、昵称、年级信息
    postStudent(params: object) {
        return axios.post('/a/u/student', qs.stringify(params));
    },
    // 上传头像
    postImg(file: any) {
        return axios.post('/a/u/student/img', qs.stringify(file));
    },
    // 登录
    postLogin(params: object) {
        return axios.post('/a/student/login', qs.stringify(params));
    },
};
